<template>
  <div>
   
    <dashboardNav @nav-update-data="updateData" @update-location="updateData"/>
     <div class="container">
      <h2 class="head">Hourly Forecast</h2>
    </div>
    <div aria-busy="true" v-if="loading" class="load"></div>
    <div v-else class="container" v-for="(details) in hours" :key="details.datetimeEpoch">
      <hourlyWeatherCard :date="details.datetime" :temp="details.temp" :visibility="details.visibility" :icon="details.icon" :desc="details.conditions"/><br/>
    </div>

  </div>
</template>

<script>
// @ is an alias to /src
import HelloWorld from '@/components/HelloWorld.vue'
import mainIndex from "@/components/main.vue"
import currentWeatherCard from "@/components/currentWeatherCard.vue"
import recentWeatherCard from "@/components/recentWeatherCard.vue"
import detailWeatherCard from "@/components/detailWeatherCard.vue"
import hourlyWeatherCard from "@/components/hourlyWeatherCard.vue"
import sunCard from "@/components/sunCard.vue"
import dayWeatherCard from "@/components/dayWeatherCard.vue"
import chart from "@/components/chart.vue"
import Settings from "@/components/settings.vue"
import dashboardNav from "@/components/dashboardNav.vue"
import hubble from "../hubble"

export default {

  name: 'HourlyForecastView',
  data(){
    return{
        hourforecast:[],
        hours:[],
        longitude:0,
        latitude:0,
        hourtime:[],
        hourtemp:[],
        loading:false
    }
  },
  components: {
    HelloWorld,
    mainIndex,
    currentWeatherCard,
    recentWeatherCard,
    detailWeatherCard,
    hourlyWeatherCard,
    sunCard,
    dayWeatherCard,
    chart,
    Settings,
    dashboardNav
  },
  methods:{
    async updateData(){
      const location = this.$store.state.search
      if(location){
        this.loading=true
        this.hourforecast = await hubble.getHourlyWeather(location, this.$store.state.unit)
        this.$store.commit('resolveAddress', this.hourforecast[0].resolvedAddress)
        this.hours = this.hourforecast[0].days[0].hours
        this.loading=false
      }else{
        this.loading=true
        const location = await hubble.getLocation()
        this.longitude = location.geoplugin_longitude
        this.latitude = location.geoplugin_latitude
        this.hourforecast = await hubble.getHourlyWeather(`${this.longitude},${this.latitude}`, this.$store.state.unit)
        this.$store.commit('resolveAddress', this.hourforecast[0].resolvedAddress)
        this.hours = this.hourforecast[0].days[0].hours
        this.loading=false
      }
    }
  },
  async created(){
    const location = this.$store.state.search
    if(location){
      this.loading=true
      this.hourforecast = await hubble.getHourlyWeather(location, this.$store.state.unit)
      this.$store.commit('resolveAddress', this.hourforecast[0].resolvedAddress)
      this.hours = this.hourforecast[0].days[0].hours
      this.loading=false
    }else{
      this.loading=true
      const location = await hubble.getLocation()
      this.longitude = location.geoplugin_longitude
      this.latitude = location.geoplugin_latitude
      this.hourforecast = await hubble.getHourlyWeather(`${this.longitude},${this.latitude}`, this.$store.state.unit)
      this.$store.commit('resolveAddress', this.hourforecast[0].resolvedAddress)
      this.hours = this.hourforecast[0].days[0].hours
      this.loading=false
      //console.log(this.hours)
      
    }
  }
}
</script>

<style scoped>
.head{
  text-align: left;
  color: rgba(0,0,0,0.9);
}
</style>

